// ./silkyCSS/modules/_colors.scss

// ------------------------------------------------
// MODULE VARIABLES
// ------------------------------------------------

// ||| COLORS VARIABLES ||||||||||
$color-1:         #18bfef;
$color-2:         #f6f6f6;
$color-3:         #717981;
$color-4:         #4e403d;
$color-5:         #212931;
$color-block-dim: 120px;

// -------------------------------------------------
// MODULE SPECIFICATIONS
// ------------------------------------------------

// ||| COLORS BLOCKS ||||||||||
.color-block {
  width:   $color-block-dim;
  height:  $color-block-dim;
  display: inline-block;

  &.color-1 {
    background: $color-1;
  }
  &.color-2 {
    background: $color-2;
  }
  &.color-2 {
    background: $color-2;
  }
  &.color-3 {
    background: $color-3;
  }
  &.color-4 {
    background: $color-4;
  }
  &.color-5 {
    background: $color-5;
  }
}
